@import '../../../style/variables.scss';


.single-comic {
  margin-top: 50px;
  display: grid;
  grid-template-columns: 293px 550px auto;
  column-gap: 50px;
  align-items: start;
}

.single-comic__char-img {
  width: 293px;
  height: 293px;
}

.single-comic__name {
  font-weight: bold;
  font-size: 22px;
  line-height: 29px;
}

.single-comic__descr {
  font-size: 18px;
  line-height: 24px;
  margin-top: 25px;
}

.single__charactersBTNS {
  position: absolute;
  bottom: 0;
  z-index: 1;
}